.departure {
  padding-bottom: 12px;
  @include media-breakpoint-up(md) {
    padding-bottom: 24px;
  }
  @include media-breakpoint-up(xl) {
    padding-bottom: 90px;
  }
  &__title {
    margin-bottom: 24px;
    @include media-breakpoint-up(md) {
      margin-bottom: 42px;
    }
  }
  &__inner {
    position: relative;
  }
  .container {
    padding-right: 0;
    @include media-breakpoint-up(sm) {
      max-width: 540px;
    }
    @include media-breakpoint-up(md) {
      padding-right: 0;
      margin-left: calc((100vw - 768px)/2);
      max-width: calc(768px + (100vw - 768px / 2));
    }
    @include media-breakpoint-up(lg) {
      margin-left: calc((100vw - 960px)/2);
      max-width: calc(960px + (100vw - 960px / 2));
    }
    @include media-breakpoint-up(xl) {
      padding-left: 96px;
      max-width: 100%;
      margin-left: 0;
    }
  }
  &__slider {
    overflow: hidden;
    margin-bottom: 0;
    padding: 0;
    font-size: 0;
  }
  .slick-track {
    @include media-breakpoint-up(xl) {
      margin-right: 0;
      margin-left: 0;
    }
  }
  .slick-slide {
    position: relative;
    margin-right: 16px;
    &:not(.slick-active) {
      &::before {
        content: '';
        position: absolute;
        top: 0;
        right: 0;
        bottom: 0;
        left: 0;
        background-color: rgba(#ebebeb, .8);
      }
    }
    @include media-breakpoint-up(md) {
      margin-right: 32px;
    }
    @include media-breakpoint-up(xl) {
      margin-right: 50px;
      width: auto;
    }
  }
  .slick-slide img {
    height: auto;
    max-height: 168px;
    @include media-breakpoint-up(md) {
      max-height: 328px;
    }
    @include media-breakpoint-up(xl) {
      min-height: 440px;
    }
  }
  .slick-dots {
    padding-top: 18px;
    @include media-breakpoint-up(md) {
      padding-top: 27px;
      justify-content: flex-start;
      margin-left: -5px;
    }
  }
  &__arrow {
    display: none;
    position: absolute;
    top: 192px;
    @include media-breakpoint-up(xl) {
      display: flex;
    }
  }
}
